<template>
  <div class="content">
    <h1>沈阳一楼盘降价被要求回收房源 官方：开发商违反《价格法》</h1>
    <img class="content-picture" src="~images/homeImg/message/2.jpg" />
    <p>据人民网“领导留言板”12月8日消息，针对网民反映沈阳祥联·大河原著开发商降价卖房的留言，沈阳市沈北新区委书记8日回复称，房产局工作人员已告知开发商禁止低于备案价大幅降价销售。</p>
    <p>沈北新区委书记10月26日在另一则回复中，则已要求楼盘中海望京府开发商，停止低于备案价销售行为，对已经低于备案件销售的房源予以回收。</p>
    <p>沈阳市去年9月发布的“沈八条”规定，对无正当理由导致价格出现较大变化的项目，责令企业限期改正，逾期未改正的，暂停该项目网签系统，改正后予以恢复。</p>
    <p>沈阳市房产局今年11月24日在回复网民时表示，开发企业不按照指导价格销售房屋，实际上是违反了《价格法》以及国家关于“明码标价”的有关规定。不过，只暂停开发企业网签备案，并未充分发挥行政效能，看似制约开发企业，实际上对购房人的合法权益产生了负面影响，只会进一步延长购房人备案时间，甚至引起更大规模的诉访。</p>
    <p>
      <strong>市民：政府文件规定，下浮不得超过5%</strong>
    </p>
    <p>网民12月5日在人民网向辽宁沈阳沈北新区委书记留言称，我是购买祥联·大河原著洋房的业主，当时购房价是每平9600元，现在刚下房，开发商降价，洋房8700元每平，远远低于房产局标定不能低于的5%标准，请求按照如今的价格给予补偿，或者退房，购房产生的契税和利息由开发商承担。</p>
    <p>
      沈阳市沈北新区委书记12月8日回复称，留言已收悉，沈北新区区委十分重视，立即责成相关职能部门进行调查了解。经房产局工作人员与开发商联系，
      <strong>已告知开发商禁止低于备案价大幅降价销售</strong>，企业销售中的特殊情况必须上报。企业销售行为，必须符合行业销售规定。
    </p>
    <p>另有网民11月14日投诉沈北新区龙湖恒联桃李芳华洋房降价，称购房价每平不到13000元，现在过了五个月就恶意降价，高层8000元每平，洋房1万元每平，远远低于房产局标定不能低于的5%标准。对此，沈北新区委书记11月16日作出回复，内容与上述12月8日的回复相同。</p>
    <p>此前，沈北新区已要求一家开发商对低于备案件销售的房源予以回收。</p>
    <p>有网民10月20日在人民网投诉称，其2020年购买中海望京府房产，2021年10月开发商无故降价，从原来的1.2万元起售降至1万元，最低9000元，这低于政府指导文件下浮的5%标准，已经下浮17%，购房差价损失36万，相当于其个人6年收入还得不吃不喝。</p>
    <p>
      对此，沈北新区委书记10月26日回复称，由信访局牵头会同房产局、市场局对中海望京府低于备案价销售的情况进行了处理，
      <strong>现已要求企业：停止低于备案价销售行为，对已经低于备案件销售的房源予以回收</strong>。
    </p>
    <p>上述市民投诉中提到的降价不能超过5%的标准，出自什么地方？</p>
    <p>
      沈阳市苏家屯区政府9月23日在沈阳市政府网站“市长信箱”回复市民投诉“华润静安府楼盘恶意降价”时提到：按照沈阳市出台的“
      <strong>沈八条</strong>”规定，商品房在销售时要在备案价格的合理区间内进行销售，超出备案价格的合理区间，将不予以备案，因此低价购房者超出备案价合理区间的是不能进行备案的。同时，市、区两级房产部门已对“静安府”特价销售行为紧急叫停，现已恢复正常销售。
    </p>
    <p>
      不过，沈阳市苏家屯区政府提到的“沈八条”，并未具体规定降价幅度5%的问题。2020年9月6日，沈阳市房产局联合八部门共同签发的《关于进一步促进我市房地产市场平稳健康发展的通知》（沈房发〔2020〕14号，简称“
      <strong>沈八条</strong>”）只是提到，以项目为单位，实时监测商品住房销售价格运行情况，项目价格变化较大的，及时约谈房地产开发企业负责人，要求企业说明原因。对无正当理由导致价格出现较大变化的项目，责令企业限期改正，逾期未改正的，暂停该项目网签系统，改正后予以恢复。
    </p>
    <p>
      澎湃新闻注意到，沈阳市政府办公厅2017年8月7日作出的《关于加强房地产市场调控工作的通知》提到，对本通知发布后新申请预售许可证的项目，按照一房一价制度申报房源价格，6个月内不得调整申报价格，6个月后可申请下调申报价格；
      <strong>备案价格不得超过申报价格且下浮不得超过5%，对备案价格不符合条件的商品住房，不予办理商品房合同网签备案</strong>。
    </p>
    <p>
      <strong>市房产局：不按指导价销售，违反《价格法》</strong>
    </p>
    <p>相比于沈北新区和苏家屯区对于开发商降价行为的回复措施，沈阳市房产局相对缓和。</p>
    <p>
      沈阳市房产局11月24日在人民网回复网民关于“浑南区荣盛华府通过中介变相降价销售，原价1.3万每平，部分房源变相降价为每平1.05万元左右”的留言称，我局已约谈荣盛华府项目负责人，该公司表示，由于项目拖欠施工款及农民工工资，加上近期房地产市场下行，公司回款困难，因此确实存在大幅降价销售情况。鉴于目前沈阳市房地产市场下行，临近年底各开发公司面临支付施工工程款、农民工工资等实际问题，我局
      <strong>要求开发商在自身积极寻求解决资金问题的基础上，控制销售节奏和降价幅度，尽可能考虑老业主的利益和情绪</strong>。
    </p>
    <p>11月24日当天，沈阳市房产局在人民网回复另外一位网民关于“荣盛华府从2020年10月每平1.35万元降价至2021年7月每平1万元，损失35万相当于7年收入”的投诉时也表示，我局已约谈荣盛华府项目负责人，该公司表示，由于市场下行，近期将一些特殊楼层作为工程抵账房出售。</p>
    <p>
      沈阳市房产局对这位网民表示，您主要反映开发企业未按政府备案价格销售商品房的一类问题，根据《关于进一步促进我市房地产市场平稳健康发展的通知》（沈房发〔2020〕14号）第七条规定：以项目为单位，实时监测商品住房销售价格运行情况，项目价格变化较大的，及时约谈房地产开发企业负责人，要求企业说明原因。对无正当理由导致价格出现较大变化的项目，责令企业限期改正，逾期未改正的，暂停该项目网签系统，改正后予以恢复。
      <strong>我们认为开发企业不按照指导价格销售房屋，实际上是违反了《价格法》以及国家关于“明码标价”的有关规定。</strong>
    </p>
    <p>
      不过，沈阳市房产局在回复中表示，
      <strong>只暂停开发企业网签备案，并未充分发挥行政效能，看似制约开发企业，实际上对购房人的合法权益产生了负面影响，未解决实际问题，只会进一步延长购房人备案时间，开发企业也会以无法备案为由推脱责任，甚至引起更大规模的诉访。</strong>
    </p>
    <p>沈阳市房产局最后表示，下一步，我局将联合市场监督管理部门对企业违法违规行为依法进行行政处罚，切实防止再次发生类似行为，进一步保护购房人合法权益。</p>
  </div>
